import React from 'react';
import './App.css';
import Quiz from './Quiz';
import { isMethod, thisExpression } from '@babel/types';
import _hearts from './gfx/hearts.png';
import _icons from './gfx/icons.png';
import _life from './gfx/life.png';
import { COPYFILE_EXCL } from 'constants';
import _words from './words.js';

let canvas, ctx, entities = [];
let icons, hearts, life;
let flash = { active: false, duration: 0, maxdur: 12, dir: 1 };
let words = [];

class Heart {
  constructor(x, y) {
    this.x = x;
    this.y = y;
    this.speed = 8;
    this.maxlifetime = Math.floor(Math.random() * 3000) + 1400;
    this.lifetime = this.maxlifetime;
    this.image = Math.floor(Math.random() * (hearts.width / 64));
  }

  update() {
    this.y -= this.speed;

    if (this.y <= -50)
      this.destroyed = true;

    this.lifetime -= 1000 / 60;
    if (this.lifetime <= 0)
      this.destroyed = true;
  }

  draw() {
    if (this.lifetime / this.maxlifetime <= 0.0)
      this.destroyed = true;
    else {
      ctx.globalAlpha = this.lifetime / this.maxlifetime;
      ctx.drawImage(hearts, 64 * this.image, 0, 64, 64, this.x, this.y, 64, 64);
    }
    ctx.globalAlpha = 1;
  }
}

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      lifes: 0,
      word: { key: "", answer: "" },
      cooldown: 120,
      amount: 1
    }

    this.randomize = this.randomize.bind(this);
    this.answer = this.answer.bind(this);
    this.correct = this.correct.bind(this);
    this.wrong = this.wrong.bind(this);
    this.spawn = this.spawn.bind(this);
  }

  randomize() {
    let w = words[Math.floor(Math.random() * words.length)];
    this.setState({ word: w })
  }

  componentDidMount() {
    words = _words.words;
    this.randomize();

    canvas = document.getElementsByTagName("canvas")[0];
    ctx = canvas.getContext("2d");

    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    icons = new Image();
    icons.src = _icons;

    hearts = new Image();
    hearts.src = _hearts;

    life = new Image();
    life.src = _life;

    window.addEventListener("resize", (e) => {
      canvas.width = document.getElementById("quiz-wrapper").clientWidth;
      // canvas.height = document.getElementById("quiz-wrapper").clientHeight;
      tick();
    })

    let _app = this;

    let cldw = 0;
    function tick() {
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      if (flash.active) {
        ctx.fillStyle = `rgba(255, 0, 0, ${flash.duration / flash.maxdur})`;
        ctx.fillRect(0, 0, canvas.width, canvas.height);

        flash.duration += 1 * flash.dir;
        if (flash.duration >= flash.maxdur)
          flash.dir = -1;
        if (flash.duration <= 0) {
          flash.active = false;
          flash.duration = 0;
          flash.dir = 1;
        }
      }

      for (let i = 0; i < entities.length; i++) {
        entities[i].update();
        entities[i].draw();
      }

      let _temp = [];
      for (let i = 0; i < entities.length; i++) {
        if (!entities[i].destroyed)
          _temp.push(entities[i]);
      }
      entities = _temp;

      for (let i = 0; i < _app.state.lifes; i++) {
        ctx.drawImage(life, 10 + i * 32, 10);
      }

      cldw++;
      if (cldw >= _app.state.cooldown) {
        _app.spawn(1);
        cldw = 0;
      }
    }

    setInterval(tick, 1000 / 60);
  }

  spawn(num) {
    for (let i = 0; i < num; i++) {
      entities.push(new Heart(Math.floor(Math.random() * (canvas.width + 50)) - 25, canvas.height + Math.floor(Math.random() * 100)));
    }
  }

  answer(val) {
    if (this.state.word.answer instanceof Array) {
      for (let i = 0; i < this.state.word.answer.length; i++) {
        if (this.state.word.answer[i].toLowerCase() == val.toLowerCase()) {
          this.correct();
          return;
        }
      }
      this.wrong();
    }
    else {
      if (val.toLowerCase() == this.state.word.answer.toLowerCase())
        this.correct();
      else
        this.wrong();
    }
    console.log(val, this.state.word.answer);
  }

  correct() {
    console.log(words.length);
    let _t = [];
    for (let i = 0; i < words.length; i++) {
      if (words[i] != this.state.word)
        _t.push(words[i]);
    }
    words = _t;
    this.randomize();
    console.log(words.length);

    if (this.state.cooldown > 10)
      this.setState({ cooldown: this.state.cooldown - 10 })
    if (Math.floor(Math.random() * 100) < 50) {
      this.setState({ amount: this.state.amount + 1 });
    }
  }

  wrong() {
    // this.setState({ lifes: this.state.lifes - 1 });

    flash.active = true;
    flash.duration = 0;
    flash.dir = 1;

    this.setState({ cooldown: 120, amount: 0 });

    this.randomize();
  }

  render() {
    return (
      <div>
        {/* <p style={{ position: "absolute", top: "0px", left: "8px"}}>{this.state.cooldown}|{this.state.amount}</p> */}
        <canvas></canvas>
        <Quiz lifes={this.state.lifes} word={this.state.word} answer={(val) => { this.answer(val) }} />
      </div>
    );
  }
}

export default App;
