import React from 'react';

class Quiz extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div id="quiz-wrapper">
                {(this.props.lifes >= 0) ?
                    <div className="quiz-content">
                        <div className="quiz-box">
                            <p>{this.props.word.key}</p>
                        </div>
                        <input placeholder={"Odpowiedź"} className="quiz-input" onKeyDown={(e) => {
                            if (e.keyCode == 13) {
                                if (e.target.value.trim()){
                                    this.props.answer(e.target.value.trim());
                                    e.target.value = "";
                                }
                            }
                        }} />
                    </div> : <div className="quiz-box" style={{ margin: "0px 20px" }}>
                        <p>Gra skończona, spróbuj ponownie!</p>
                    </div>
                }
            </div>
        );
    }
}

export default Quiz;